body {
  background-color: #000;
}

ul {
  list-style: none;
}

.calculator-container {
  color: #fff;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
  display: flex;
}

.calculator {
  border-radius: 12px;
  max-width: 15em;
  margin-top: 2em;
  margin-left: auto;
  margin-right: auto;
  overflow: hidden;
  box-shadow: 0 0 40px #00000026;
}

.calculator__display {
  color: #fff;
  text-align: right;
  background-color: #222;
  padding: .5em .75em;
  font-size: 1.71429em;
}

.calculator__keys {
  grid-gap: 1px;
  background-color: #999;
  grid-template-columns: repeat(4, 1fr);
  display: grid;
}

.calculator__keys > * {
  text-align: center;
  background-color: #fff;
  padding: .5em 1.25em;
  position: relative;
}

.calculator__keys > :active:before, .calculator__keys > .is-depressed:before {
  content: "";
  opacity: .3;
  z-index: 1;
  background-color: #0003;
  position: absolute;
  inset: 0;
  box-shadow: inset 0 0 6px #00000080;
}

.key--operator {
  background-color: #eee;
}

.key--equal {
  background-image: linear-gradient(#fe886a, #ff7033);
  grid-area: 2 / -2 / span 4;
}

